<h1 id="对账单" tabindex="-1"><a class="header-anchor" href="#对账单" aria-hidden="true">#</a> 对账单</h1>
<h2 id="下载对账单" tabindex="-1"><a class="header-anchor" href="#下载对账单" aria-hidden="true">#</a> 下载对账单</h2>
<blockquote>
<p>调用参数正确会返回一个 <code>EasyWeChat\Kernel\Http\StreamResponse</code> 对象，否则会返回相应错误信息</p>
</blockquote>
<p>Example:</p>
<div class="language-php ext-php line-numbers-mode"><pre v-pre class="language-php"><code><span class="token variable">$bill</span> <span class="token operator">=</span> <span class="token variable">$app</span><span class="token operator">-></span><span class="token property">bill</span><span class="token operator">-></span><span class="token function">get</span><span class="token punctuation">(</span><span class="token string single-quoted-string">'20140603'</span><span class="token punctuation">)</span><span class="token punctuation">;</span> <span class="token comment">// type: ALL</span>
<span class="token comment">// or</span>
<span class="token variable">$bill</span> <span class="token operator">=</span> <span class="token variable">$app</span><span class="token operator">-></span><span class="token property">bill</span><span class="token operator">-></span><span class="token function">get</span><span class="token punctuation">(</span><span class="token string single-quoted-string">'20140603'</span><span class="token punctuation">,</span> <span class="token string single-quoted-string">'SUCCESS'</span><span class="token punctuation">)</span><span class="token punctuation">;</span> <span class="token comment">// type: SUCCESS</span>

<span class="token comment">// 调用正确，`$bill` 为 csv 格式的内容，保存为文件：</span>
<span class="token variable">$bill</span><span class="token operator">-></span><span class="token function">saveAs</span><span class="token punctuation">(</span><span class="token string single-quoted-string">'your/path/to'</span><span class="token punctuation">,</span> <span class="token string single-quoted-string">'file-20140603.csv'</span><span class="token punctuation">)</span><span class="token punctuation">;</span>
</code></pre><div class="line-numbers"><span class="line-number">1</span><br><span class="line-number">2</span><br><span class="line-number">3</span><br><span class="line-number">4</span><br><span class="line-number">5</span><br><span class="line-number">6</span><br></div></div><p>第二个参数为账单类型，参考：https://pay.weixin.qq.com/wiki/doc/api/jsapi.php?chapter=9_6 中 <code>bill_type</code>，默认为 <code>ALL</code></p>
